        try {
          console.log('sendTransaction-----22method>>', sendData.method)

          let chainId = 'eip155:1';
          if (this.selectedChain) {
            chainId = this.selectedChain;
            console.log('sendTransaction-----this.selectedChain>>', this.selectedChain)
          }
          console.log("windowOpen[click_1]: ", Date.now())
          if (chainId == 'sui:mainnet') {
            const adapter = new SuiAdapter(window.provider)
            var suiResult = 'invalid result'
            if (this.currentSuiMethod == 'sui_signTransaction') {
              suiResult = await adapter.signTransaction(this.currentSuiTransaction)
              this.testboc = JSON.stringify(suiResult)

            } else if (this.currentSuiMethod == 'sui_signAndExecuteTransaction') {
              suiResult = await adapter.signAndExecuteTransaction(this.currentSuiTransaction)
              this.testboc = JSON.stringify(suiResult)

            } else if (this.currentSuiMethod == 'sui_signMessage') {
              console.log('signMessage----', this.currentSuiTransaction)
              suiResult = await adapter.signMessage(this.currentSuiTransaction)
              this.testboc = JSON.stringify(suiResult)

            } else if (this.currentSuiMethod == 'sui_signPersonalMessage') {
              console.log('signPersonalMessage----', this.currentSuiTransaction)
              suiResult = await adapter.signPersonalMessage(this.currentSuiTransaction)
              this.testboc = JSON.stringify(suiResult)

            } else if (this.currentSuiMethod == 'sui_testRpc') {
              console.log('test rpc----', this.currentSuiTransaction)
              console.log(`请求数据发送 rpc ----->> ${JSON.stringify(sendData)}`)
              const res = await window.provider.request(sendData, chainId)
              console.log(`请求数据回调 rpc ----->> ${JSON.stringify(result)}`)
              if (typeof res == "string") {
                this.testboc = res
              } else {
                this.testboc = JSON.stringify(res)
              }
            }
            console.log("windowOpen[click_2]: ", Date.now())
            return
          }

          console.log(`请求数据发送 sendTransaction ----->> ${JSON.stringify(sendData)}`)
          var result = await window.provider.request(sendData, chainId)
          console.log(`请求数据回调 sendTransaction ----->> ${JSON.stringify(result)}`)

          console.log("windowOpen[click_3]: ", Date.now())
          if (typeof result == "string") {
            this.testboc = result
          } else {
            this.testboc = JSON.stringify(result)
          }
          console.log('方法数据回调 --this.testboc--', this.testboc)
        } catch (error) {
          console.error('方法数据回调失败 --Error--', error.message)

          if (error.code === OKX_CONNECT_ERROR_CODES.USER_REJECTS_ERROR) {
            this.testboc = error.message
          } else {
            this.testboc = "签名错误！" + error.message
          }
        }
      } else {
        alert("交易数据为空，请选则或输入交易数据。")
      }
    },

    async testTonSdk() {
      // 1. connect
      //  create sub providers for each namespace/chain
      let nameSpacesChains = []
      let optSpaceChains = []

      let solanaChains = [
        "solana:5eykt4UsFv8P8NJdTREpY1vzqKqZKvdp",
        "solana:4uhcVJyU9pJkvQyS88uRDiswHXSCkY3z",
        "sonic:4uhcVJyU9pJkvQyS88uRDiswHXSCkY3z"
      ];
      let hasEip155, hasSolana, hasSui = false

      let suiChains = [
        "sui:mainnet",
      ];
      console.log(`chains  ===> ${chains.values()}`)
      chains.forEach(function (chainId) {
        if (solanaChains.includes(chainId)) {
          nameSpacesChains.push(chainId)
          hasSolana = true
          return
        }
        if (typeof chainId == 'string' && chainId.startsWith("svm")) {
          nameSpacesChains.push(chainId)
          hasSolana = true
          return
        }
        if (suiChains.includes(chainId)) {
          console.log(`nameSpacesChains.push  ===> ${chainId}`)
          nameSpacesChains.push(chainId)
          hasSui = true
          return
        }
        hasEip155 = true
        nameSpacesChains.push(`eip155:${chainId}`)
      })
      optionChains.forEach(function (chainId) {
        if (solanaChains.includes(chainId)) {
          optSpaceChains.push(chainId)
          return
        }
        if (typeof chainId == 'string' && chainId.startsWith("svm")) {
          nameSpacesChains.push(chainId)
          hasSolana = true
          return
        }
        if (suiChains.includes(chainId)) {
          optSpaceChains.push(chainId)
          return
        }
        optSpaceChains.push(`eip155:${chainId}`)
      })

      console.log(`namespaceChains  ===> ${JSON.stringify(nameSpacesChains)}`)
      var connectData = {};
      if (nameSpacesChains && nameSpacesChains.length > 0) {
        var requireData = {}
        if (hasEip155) {
          requireData.eip155 = {
            chains: nameSpacesChains.filter(item => item.startsWith("eip155")),
            defaultChain: nameSpacesChains.filter(item => item.startsWith("eip155"))[0]
          }
        }
        if (hasSolana) {
          requireData.solana = {
            chains: nameSpacesChains.filter(item => item.startsWith("solana") || item.startsWith("sonic") || item.startsWith("svm")),
            defaultChain: nameSpacesChains.filter(item => item.startsWith("solana") || item.startsWith("sonic") || item.startsWith("svm"))[0]
          }
        }
        if (hasSui) {
          requireData.sui = {
            chains: nameSpacesChains.filter(item => item.startsWith("sui")),
            defaultChain: nameSpacesChains.filter(item => item.startsWith("sui"))[0]
          }
        }
        console.log(`requireData  ===> ${JSON.stringify(requireData)}`)
        connectData['namespaces'] = requireData;
      }

      if (optSpaceChains && optSpaceChains.length > 0) {
        var optData = {
          eip155: {
            chains: optSpaceChains.filter(item => item.startsWith("eip155")),
            defaultChain: optSpaceChains.filter(item => item.startsWith("eip155"))[0]
          }
        }
        connectData['optionalNamespaces'] = optData;
      }

      if (nameSpacesChains.length === 0 && optSpaceChains.length === 0) { //未选择链
        alert("请选择一条链");
        return
      }
      console.log("=====>connectData ")
      console.log(connectData)
      await window.provider.connect(connectData).then(() => {
        // 2. set defaul chain
        this.walletAddress = "connected";
        if (this.isPC()) {
          this.showModal = false;
        }
      }).catch(error => {
        console.error("发生了错误：", error)
      });
    },

